import time from "../../common/time.js"
export default {
	props:{
		imgUrl:String,
		name:String,
		timeStamp:String,
		playIndex:Number,
		showEdit: Boolean,
		index:Number,
		currentTime:String,
		duration:String,
		selectedAll:Boolean
	},
	data(){
		return{
			selected:false
		}
	},
	computed:{
		formatTime(){ //

		return 	time.gettime(Number(this.timeStamp))
		}
	},
	methods:{
		longpresHandler(){
			this.$emit('longpresHandler')
		},
		tapHandler(){
			if(this.showEdit){
				this.selected = !this.selected 
				this.selected && this.$emit('selsect',this.index)
				!this.selected && this.$emit('noSelect',this.index)
			}
		}
	},
}

